import linIconButton from './lin-icon-button.vue';
import linPopup from "./lin-popup.vue";
export default {
	props: {
		title: {
			type: [String,Boolean],
			default:false 
		},
		fixed:{
			type:Boolean,
			default:true
		},
		noreadnum:{
			type:Number,
			default:0
		}
	},
	components:{
		linIconButton,
		linPopup
	},
	data() {
		return {
			statusBarHeight:0,
			navBarHeight:0,
			menus:[
				{
					name:"发起群聊",
					event:"",
					icon:"\ue633"
				},
				{
					name:"添加好友",
					event:"",
					icon:"\ue65d"
				},
				{
					name:"扫一扫",
					event:"",
					icon:"\ue614"
				},
				{
					name:"收付款",
					event:"",
					icon:"\ue66c"
				},
				{
					name:"帮助与反馈",
					event:"",
					icon:"\ue66c"
				}
			],
		}
	},
	mounted() {



		this.navBarHeight = this.statusBarHeight + uni.upx2px(90)
	},
	computed: {
		fixedStyle() {
			return `height:${this.navBarHeight}px`
		},
		getTitle(){
			let noreadnum = this.noreadnum > 0 ? '('+this.noreadnum+')' : ''
			return this.title + noreadnum
		}
	},
	methods: {
		openExtend() {
			this.$refs.extendNav.show(uni.upx2px(415),uni.upx2px(150))
		}
	},
}
